import React from 'react'
import Grid from '@mui/material/Grid';

export default function OfferingList(props) {

	const { header, body, image, color } = props.l;

	return (
		<Grid container className="offeringClass">
			<Grid className="grid" item xs={12} md={6}>
				<h2 style={{color: color}}>{header}</h2>
				{props.width > 900 && <p>{body}</p>}
			</Grid>
			<Grid className="grid" item xs={12} md={6}>
				<img src={image} alt={header} />
				{props.width < 900 && <p>{body}</p>}
			</Grid>
		</Grid>
	)
}